.wrap-form
	@apply relative gap-y-[26px] grid gap-x-4
	.wpcf7-form
		@apply  gap-y-[26px] grid gap-x-5
	.fa-exclamation-triangle,.wpcf7-not-valid-tip
		@apply absolute text-red font-bold text-base
		bottom: -22px
		left: 0
		@media screen and ( max-width: 768px)
			font-size: 12px
			@apply font-bold
			bottom: -24px
		&:before
			font-family: 'Font Awesome 6 Pro' !important
	.wpcf7-form-control-wrap
		@apply block static h-full
	.wpcf7-response-output
		@apply text-red text-16 relative 
		z-index: 9999
		border: none !important
		margin: 0 !important
		grid-column: span 2 / span 2
		padding:  0 !important
	.col
		@apply mb-4 relative
		&:last-child
			@apply mb-6
	.attach-file
		.codedropz-upload-handler
			height: 50px
			.codedropz-upload-container
				padding: 4px
		.codedropz-upload-inner 
			@apply text-16 x-center
			*
				@apply text-16
			.cd-upload-btn
				@apply text-16
			span
				@apply block mx-2
	.form-group
		@apply relative overflow-visible
		grid-column: span 1 / span 1
		&.col-span-2
			grid-column: span 2 / span 2
		>span
			@apply w-full
		input,textarea,select
			min-height: 40px
			height: 40px
			@apply w-full transition font-normal text-base  py-2  overflow-hidden  px-4 border border-grey-100
			outline: none !important
			&::placeholder
				@apply text-base text-grey-500 
	
		select
			@apply text-16
			option
				@apply text-16
	.form-group
		@media screen and ( max-width: 1024px)
			grid-column: span 2 / span 2
		&.full-width
			@apply col-span-4
		textarea
			@apply p-4 w-full
			height: 7rem
			@screen lg
				height: r(120px)
	.custom-select
		@apply relative
		select
			@apply text-16  pt-0 pb-3
	.custom-datepicker
		@apply relative 
		width: 100% !important
		&::after
			content: '\f133'
			font-family: 'Font Awesome 6 Pro'
			@apply block absolute-y pointer-events-none right-[4px] z-10  transition leading-none font-light  
			height: 16px
			width: 12px
			font-size: 16px
		input
			width: 100% !important
			-webkit-appearance: none
			-moz-appearance: none
			@apply   pointer-events-auto
			&::placeholder
				@apply not-italic hidden
			&::-webkit-calendar-picker-indicator 
				color: transparent
				background: none
				background: transparent
				bottom: 0
				color: transparent
				cursor: pointer
				height: auto
				left: 0
				position: absolute
				right: 0
				top: 0
				width: auto


.date-picker
	@apply rounded overflow-hidden w-full relative
	input[type='date']
		@apply block overflow-hidden bg-primary-50 p-1 text-grey-500 h-[46px] text-16 px-5 
		-webkit-appearance: none
		margin-top: 0px
		text-align: center
		align-self: center
		vertical-align: bottom
		@screen lg
			@apply h-[44px]
		&::-webkit-calendar-picker-indicator 
			@apply absolute-center w-full h-full opacity-0
		&::after
			content: ''
			@apply block absolute-y pointer-events-none right-[20px]  z-10  transition 
			background-image: url("data:image/svg+xml;charset=utf-8,<svg width='14' height='16' viewBox='0 0 14 16' fill='none' xmlns='http://www.w3.org/2000/svg'><path d='M3.5 0C3.75 0 4 0.25 4 0.5V2H10V0.5C10 0.25 10.2188 0 10.5 0C10.75 0 11 0.25 11 0.5V2H12C13.0938 2 14 2.90625 14 4V14C14 15.125 13.0938 16 12 16H2C0.875 16 0 15.125 0 14V4C0 2.90625 0.875 2 2 2H3V0.5C3 0.25 3.21875 0 3.5 0ZM13 6H1V14C1 14.5625 1.4375 15 2 15H12C12.5312 15 13 14.5625 13 14V6ZM12 3H2C1.4375 3 1 3.46875 1 4V5H13V4C13 3.46875 12.5312 3 12 3Z' fill='%238CC241'/></svg>")
			background-repeat: no-repeat
			background-size: contain !important
			background-position: center
			width: 16px
			height: 16px
	@media (max-width: 767px)  // mobile devices
		@supports (-webkit-overflow-scrolling: touch)  //only safari
			input[type="date"]:before 
				color: #7C7C7C
				@apply absolute-center
				content: attr(placeholder)
			input[type="date"]:focus:before
				color: #7C7C7C
				content: "" !important
			input[type="date"] 
				-webkit-text-fill-color: #7C7C7C
				-webkit-opacity: 1
	input::-webkit-datetime-edit 
		display: block
		padding: 0
	input::-webkit-date-and-time-value
		@apply pt-[12px]
		text-align: center
		align-self: bottom
		vertical-align: bottom
.input-group,.form-password
	@apply relative w-full
	.reveal-password
		@apply absolute top-50 right-[20px]  text-grey-500 text-20 font-light
		em
			@apply  font-light
			font-size: 20px
.pwd-input-group
	input
		@apply w-full
	&.show
		.reveal-password
			em
				&::before
					content: '\f06e' !important
.frm-btnwrap
	@apply col-span-2  x-center
.wpcf7 form.wpcf7-form.submitting button 
	opacity: 0.5  !important
	pointer-events: none !important
.subscribe
	.subscribefrm
		@apply relative 
	input
		@apply text-16 h-[42px] w-full bg-transparent  px-5 pr-[40px] rounded-full overflow-hidden
		&::placeholder
			@apply  text-14 
	button
		@apply absolute top-[4px] right-[4px] rounded-full overflow-hidden w-[32px]  h-[32px]  x-center  transition 
		i,em
			@apply text-14  transition text-white font-light