@layer components
	.desc,.zone-desc
		@apply leading-140
		strong
			@apply font-bold
		ul
			list-style-type: disc
			li
				padding-left: 20px
				@apply mb-3 block relative
				&::before
					content: ''
					@apply inline-block absolute  left-0  pointer-events-none z-10  transition rounded-full  top-[12px]  w-[6px] h-[6px] min-w-[6px] bg-grey-900 
			ul
				list-style-type: disc
				li
					padding-left: 20px
					@apply mb-1 block
					&::before
						content: ''
						@apply inline-block absolute left-0  pointer-events-none z-10  transition top-[6px]  w-[6px] h-[1px]  min-w-[3px] bg-grey-900 
		ol 
			counter-reset: item
			li
				display: block
				&:before 
					content: counters(item, ".") ". "
					counter-increment: item
		ol[style*="list-style-type: lower-alpha;"] li:before 
				content: counters(item, ".", lower-alpha) ". "
				counter-increment: item
.briefcontent
	@apply text-lg font-bold leading-140
	*
		@apply text-lg font-bold leading-140
	span
		@apply text-primary-2 mr-1
.fullcontent
	@apply leading-140 text-base
	*
		@apply leading-140 text-base
	img,iframe
		@apply block mx-auto   overflow-hidden  w-full my-4
		@screen max-sm
			@apply h-full !#{important}
	p
		@apply mb-4 text-lg
		*
			@apply text-lg
		&:last-child
			@apply mb-0
	a
		@apply text-primary-1
		&:hover
			@apply underline
	figure
		@apply mx-auto my-4
		img
			@apply m-0  overflow-hidden
		figcaption
			@apply font-light text-base italic text-center my-2
	hr
		@apply block my-1 text-grey-100
	ul
		list-style-type: disc
		li
			padding-left: 20px
			@apply mb-3 block relative
			&::before
				content: ''
				@apply inline-block absolute  left-0  pointer-events-none z-10  transition rounded-full  top-[10px]  w-[6px] h-[6px] min-w-[6px] bg-grey-900 
		ul
			list-style-type: disc
			li
				padding-left: 20px
				@apply mb-1 block
				&::before
					content: ''
					@apply inline-block absolute left-0  pointer-events-none z-10  transition top-[6px]  w-[6px] h-[1px]  min-w-[3px] bg-grey-900 
	ol 
		counter-reset: item
		li
			display: block
			&:before 
				content: counters(item, ".") ". "
				counter-increment: item
	ol[style*="list-style-type: lower-alpha;"] li:before 
			content: counters(item, ".", lower-alpha) ". "
			counter-increment: item
	hr
		@apply block my-6
	h2,h3,h4,h5,h6
		@apply  mb-4 font-bold  
	h2
		@apply text-3xl leading-none
		*
			font: inherit
	h3
		@apply text-2xl leading-none
		*
				font: inherit
	h4
		@apply text-xl
		*
			font: inherit
	h5
		@apply text-lg
		*
			font: inherit
	h6
		@apply text-base
		*
			font: inherit
	strong
		font-weight: 700 !important
		*
			font-weight: 700 !important
	.row
		@apply mb-0
	.table-res
		table
			@apply w-full my-5
			tr
				&:nth-child(even)
					td
						@apply bg-grey-200 bg-opacity-20
				td
					@apply border border-grey-100  text-left font-bold 
					padding: r(8px) r(12px) !important
					&:first-child
						@apply font-normal text-grey-500 align-baseline
			p
				@apply mb-0