.popup-modal
	padding: 0
	border-radius: 16px
	@apply w-full overflow-hidden
.carousel__button
	&.is-close
		@apply rounded-full overflow-hidden 
		top: 8px !important
		right: 8px !important
		width: 40px
		height: 40px
		@screen xl
			top: 16px !important
			right: 16px !important
		svg
			@apply text-primary-1 


.about-modal
	padding:  0 !important
	box-shadow: 0px 4px 4px 0px #1F222714
	@screen xl
		max-width: r(1160px)
	.img-wrap
		@screen max-md
			@apply pt-8
			a
				display: flex
				height: r(250px)
				width: 100%
				img
					width: 100%
					height: 100%
					object-fit: contain
		@screen md
			@apply w-full h-full
			a
				@apply w-full h-full flex-center
				img
					@apply w-full h-full object-cover
	.row
		@apply m-0 p-0 
		@screen md
			@apply h-full
		.col
			@apply m-0 p-0 
			@screen md
				@apply h-full
				&:first-child
					width: 44%
				&:nth-child(2)
					width: 56%
	.scrollbar-wrap
		max-height: r(250px)
		@screen xl
			max-height: r(400px)
		@media screen and ( min-width: 1400px)
			max-height: r(500px)
	.popup-modal-wrap
		@screen xl
			height: r(560px)
	@screen max-md
		.carousel__button
			&.is-close
				@apply bg-primary-3 rounded-full overflow-hidden
				top: 4px !important
				right: 4px !important
				width: 32px
				height: 32px
				svg
					@apply text-base w-[20px] text-white
	.fullcontent
		ul
			li
				&:before
					@apply w-[3px] h-[3px] min-w-[3px] top-2
.culture-modal
	max-width: r(920px)
	padding:   0 !important
	border-radius: 16px
	@screen md
		padding:   r(40px)  !important
		@apply rounded-4
	.carousel__button
		@apply z-50
	.txt
		@screen max-md
			@apply p-8
	.img
		@apply flex-center
		@screen md
			height: r(608px)
			width:  r(388px)
			min-width:  r(388px)
		@screen max-md
			@apply hidden
		img
			width: 100%
			height: 100%
			object-fit: cover
.recruit-modal
	max-width: r(1152px)
	padding:   r(32px)  !important
	border-radius: 16px
	@screen xl
		@apply rounded-8
		padding:   r(64px)  !important
	.form-title
		font-size: r(28px)
	.desc
		@apply mb-6
	.container,.container-fluid
		@apply p-0
	.btn
		em,i
			animation: none !important