body
	--header-height: 70px
	--banner-padding: #{clampSize(60)}
	@screen xl
		--banner-padding: #{clampSize(100)}
		--header-height:  #{clampSize(100)}
	@media screen and ( min-width: 1400px)
		--banner-padding: #{clampSize(120)}
		--header-height:  #{clampSize(148)}
	&.minimize
		@screen xl
			--header-height: #{clampSize(100)}
		.search-overlay
			top: var(--header-height)
