.swiper-slide
	height: auto 
.init-swiper
	--mr: 10px
	--spv: 1
	position: relative
	@screen md
		--mr: 16px
	@screen xl
		--mr:  #{clampSize(40)}
	&[data-time='0']
		.swiper-wrapper
			transition-timing-function: linear
	.swiper-slide.swiper-slide
		@apply overflow-visible
	.swiper-slide
		width: calc(calc(100% - calc(var(--mr) * calc(var(--spv) - 1)))/var(--spv))
		@apply h-auto
		&:not(:last-child)
			margin-right: var(--mr)

	&.no-gap
		--mr: 0
		.swiper-slide
			width: calc(100% / var(--spv))

	&.small-gap
		--mr: #{clampSize(10)}
	&.medium-gap
		--mr: #{clampSize(20)}
	&.medium-gap-24
		--mr: #{clampSize(24)}
	&.auto-2
		@screen md
			--spv: 2
	&.auto-3
		@screen md
			--spv: 2
		@screen lg
			--spv: 3
	&.auto-4
		@screen sm
			--spv: 2
		@screen md
			--spv: 3
		@screen xl
			--spv: 4
	&.auto-5
		--spv: 1
		@screen sm
			--spv: 2
		@screen md
			--spv: 3
		@screen lg
			--spv: 4
		@screen xl
			--spv: 5
	&.auto-6
		--spv: 1
		@screen sm
			--spv: 2
		@screen md
			--spv: 3
		@screen lg
			--spv: 4
		@screen xl
			--spv: 5
	&.show-half-mobile
		@screen max-sm
			--spv: 1.5
	&.slide-products-spacing
		--mr: #{clampSize(18)}
	.swiper-scrollbar.swiper-scrollbar
		@apply static
		@apply rounded-none w-full
		@apply  mt-10 h-[2px]
		.swiper-scrollbar-drag
			@apply rounded-none
			@apply bg-primary-2
	&.arrow-edge
		.arrow-button
			.button-prev
				@apply left-0
			.button-next
				@apply right-0
.visible-slide
	.swiper
		@apply overflow-visible

.swiper-pagination
	@apply flex-center 
	position: static !important
	left: unset !important
	right: unset !important
	bottom: unset !important
	transform: none !important
	margin-top: 20px
	@apply pointer-events-auto h-[20px] !#{important}
	background: none !important
	.swiper-pagination-bullet 
		@apply relative  bg-transparent border-4 border-transparent  transition bg-opacity-100 opacity-100  w-[16px]  min-w-[16px]  h-[16px]
		@screen 2xl
			@apply rem:w-[18px] rem:min-w-[18px] rem:h-[18px]
		&::before,&::after
			content: ''
			@apply block absolute-center pointer-events-none  z-10  rounded-full transition w-full h-full
		&::before
			@apply border opacity-0 border-primary-1  w-[16px]  min-w-[16px]  h-[16px]
			@screen 2xl
				@apply 	w-[18px]  min-w-[18px]  h-[18px]
		&::after
			@apply bg-primary-1 bg-opacity-80  w-[6px]  min-w-[6px]  h-[6px] opacity-50
			@screen 2xl
				@apply rem:w-[12px] rem:min-w-[12px] rem:h-[12px]
		&.swiper-pagination-bullet-active
			@apply bg-primary-1 inline-block 
			border: 4px solid white !important
			&::before
				@apply opacity-100 border-primary-1
			&::after
				@apply bg-primary-1


.swiper-column-1,.swiper-column-2,.swiper-xl-3,.swiper-xl-4
	.swiper-slide
		@apply h-auto
.swiper-center
	.swiper-wrapper
		@apply w-fit mx-auto

.swiper-shadow-spacing
	@screen xl
		.swiper
			padding: #{clampSize(32)}
			margin: #{clampSize(-32)}

.swiper
	&.swiper-fade
		.swiper-slide
			opacity: 0 !important
		.swiper-slide-active
			opacity: 1 !important

.dynamic-slide.swiper-slide
	width: calc(calc(100% - calc(var(--mr) * calc(var(--spv) - 1)))/var(--spv))
	@apply h-auto
	&:not(:last-child)
		margin-right: var(--mr)
