.fp-watermark
	display: none !important
body
	&.home
		footer
			display: none !important
	&:not(.home)
		&.minimize
			.nav-brand
				a
					@media screen and ( min-width: 1400px)
						height: r(80px)
					@media screen and ( min-width: 4000px)
						@apply h-20
			.search-toggle
				@apply border-primary-3 !#{important}
				&::before
					@apply text-primary-3 !#{important}
				&:hover
					@apply bg-primary-2 border-primary-2 !#{important}
					&:before
						@apply text-primary-1 !#{important}
			&.overlay-bg
				.search-toggle
					&::before
						@apply text-white !#{important}
	&.logged-in
		header
			top: 0 !important
		.account-login
			margin: 0  10px 0 4px
			@screen xl
				margin: 0  20px 0 14px 


	&.overlay-search
		header
			background: none !important
			@apply border-b border-grey-100  overflow-hidden
			&:before
				opacity: 1 !important
		.nav-primary-menu
			.nav
				>.menu-item
					&::after
						@apply w-0 
					&:hover
						&::after
							@apply w-full
					>a
						@apply text-grey-700 !#{important}
					.title
						a
							@apply text-grey-700 !#{important}
				.menu-item
					&:hover,&.current-menu-item,&.current-category-ancestor,&.current-menu-ancestor,&.current-menu-parent,&.current-category-parent,&.current-money-map-category-ancestor,&.current-post-ancestor
						&::after
							@apply w-full
						>.title
							a
								@apply text-primary-3 !#{important}
								text-shadow: 1px 0 0 #CDA966
						>a
							@apply text-primary-3 !#{important}
							text-shadow: 1px 0 0 #CDA966
							@screen xl
								.icon
									@apply text-primary-1
		.account-wishlist,.account-login
			em
				@apply text-primary-3  !#{important}
		.account-wishlist,.account-login
			&:hover
				a
					@apply bg-primary-2 !#{important}
					em
						@apply text-primary-1 !#{important}
	
		.nav-brand
			.real-logo
				@apply opacity-100 !#{important}
			.placeholder-logo
				@apply opacity-0 !#{important}
	&.page-template-Introduce
		.fancybox__viewport
			background:  white !important
	&.body-has-fp
		.back-to-top
			@apply opacity-0 pointer-events-none
		header
			transition: none !important
	
			.cointainer
				@apply relative z-40
		.sidenav-group
			&.is-visible
				.back-to-top
					@apply opacity-100 pointer-events-auto
		section
			@media screen and ( max-width: 1200px)
				&.footer 
					@apply hidden
	&.tax-product-tax,&.page-template-Introduce,&.page-template-Contact
		main
			@screen xl
				padding-top: 0 !important
		.nav-primary-menu
			.menu-item
				&:hover,&.current-menu-item,&.current-category-ancestor,&.current-menu-ancestor,&.current-menu-parent,&.current-category-parent,&.current-money-map-category-ancestor,&.current-post-ancestor
					&::after
						@apply w-0
		.account-wishlist,.account-login
			em
				@screen xl
					@apply text-white
		.account-wishlist
			&:hover
				a
					@apply bg-primary-2 !#{important}
					em
						@apply text-primary-1 !#{important}

		@screen xl 
			.nav-brand
				.real-logo
					@apply opacity-0
				.placeholder-logo
					@apply opacity-100
			header
				background: linear-gradient(180deg, rgba(0, 0, 0, 0.6) 0%, rgba(0, 0, 0, 0) 100%)
				backdrop-filter: none
				border: none !important
				&::before
					@apply opacity-0
				.nav-primary-menu
					.nav
						>.menu-item
							&::after
								@apply w-0 
							&:hover
								&::after
									@apply w-full
							>a
								@apply text-white
							.title
								a
									@apply text-white
					@screen xl
				&:hover
					background: none !important
					@apply border-b border-grey-100 
					&:before
						@apply opacity-100
					.nav-primary-menu
						.nav
							>.menu-item
								>a
									@apply text-grey-700
								.title
									a
										@apply text-grey-700
				
					.nav-brand
						.real-logo
							@apply opacity-100
						.placeholder-logo
							@apply opacity-0
			&.minimize
				header
					&::before
						@apply opacity-100
				.nav-primary-menu
					.nav
						>.menu-item
							&::after
								@apply w-0 
							&:hover
								&::after
									@apply w-full
							>a
								@apply text-grey-700 
							.title
								a
									@apply text-grey-700 
	&:not(.body-has-fp)
		.footer 
			padding-top: r(70px)
			@screen xl
				padding-top: r(120px)
		.contact-section
			padding: r(80px) 0
	&:not(.page-template-page-contact)
		.contact-btn-group
			@apply grid-cols-1 !#{important}
			.btn.btn-primary
				width: max-content

	&.fp-nav-white
		#menu-parallax
			li
				a
					.number
						@apply text-white opacity-100
					.txt
						@apply  bg-primary-1 text-white
						
	&.fp-body-blue
		header
			&::before
				@apply opacity-0 !#{important}
			&::after
				@apply opacity-100 !#{important}
		.nav-brand
			a
				img,svg
					filter: brightness(0) invert(1)
		.hamburger-inner
			@apply bg-white !#{important}
			&:before,&::after
				@apply bg-white !#{important}
		.search-toggle
			&::before
				@apply text-white
		.language-wrap
			.wpml-ls-item 
				&::after
					@apply bg-white
				span
					@apply text-white
	&.overlay-bg
		header
			&::before,&::after
				@apply opacity-0 !#{important}
		
		.hamburger-inner
			@apply bg-white !#{important}
			@screen max-xl
				@apply bg-primary-1 !#{important}
			&:before,&::after
				@screen max-xl
					@apply bg-primary-1 !#{important}
				@screen xl
					@apply bg-white !#{important}
		.search-toggle
			&::before
				@screen max-xl
					@apply text-primary-1 !#{important}
				@screen xl
					@apply text-white !#{important}
	&.fp-viewing-home_banner,&.fp-viewing-home_intro
		#menu-parallax
			li
				&.active,&:hover
					.number
						@apply text-primary-1
.body-has-fp
	main
		padding-top: 0 !important
	.nav-primary-menu
		.menu-item
			&:hover,&.current-menu-item,&.current-category-ancestor,&.current-menu-ancestor,&.current-menu-parent,&.current-category-parent,&.current-money-map-category-ancestor,&.current-post-ancestor
				&::after
					@apply w-0
	header
		&:hover
			.nav-primary-menu
				.nav
					>.menu-item
						&::after
							@apply w-0 
						&:hover
							&::after
								@apply w-full
	footer
		display: none !important

#menu-parallax
	@apply fixed   flex-col gap-2 top-1/2 -translate-y-1/2
	right: r(28px)
	z-index: 999
	align-items: flex-start
	@media screen and ( max-width: 1200px)
		@apply hidden
	br
		@apply hidden
	li
		@apply  w-fit flex justify-end items-center relative  
		&:not(:last-child)
			@apply mb-4
		&.active,&:hover
			.number
				@apply text-2xl font-bold opacity-100
		&:hover
			@apply bg-opacity-100  rounded-full 
			.txt
				@apply  bg-opacity-100 opacity-100  rounded-full  mr-3
				transform: translate(0,-50%)
		a
			
			@apply relative z-10
			transition: .4s all  ease-in-out
			.number
				@apply flex-center w-8 min-w-8  h-8 border-4 border-transparent relative text-primary-1 text-xs font-normal transition opacity-50
			
			.txt
				@apply  h-full  absolute-y  z-10 right-full text-base   font-bold  px-4 flex-center bg-white w-max pointer-events-none opacity-0 overflow-hidden rounded-l-full  text-primary-1
				transform: translate(100%,-50%)
				transition: .4s all  ease-in-out
				*
					@apply  transition font-bold   
#fullpage
	section
		@apply overflow-hidden
		@media screen and ( max-width: 1200px)
			padding: r(60px) 0
	.footer
		@screen xl
			@apply pb-10 justify-end !#{important}
.single-post

	.global-breadcrumb
		@apply bg-grey-50
.tax-du-an-category,.category-tin-tuc,.category,.single
	header
		&::before
			@apply bg-white
	main
		@screen xl
			padding-top: var(--header-height) !important
.global-breadcrumb
	.container
		@screen xl
			@apply p-0