@layer components
	// Base text mixin
	@mixin text-base
		@apply transition 
		*
			@apply transition
			font: inherit
	.heading-banner
		@apply text-4xl font-bold
		@screen sm
			@apply text-5xl 
		@screen xl
			@apply text-6xl
	.heading-1,.heading-1-up
		@apply text-4xl font-extrabold text-primary-1
		br
			@media screen and ( max-width: 1024px)
				@apply hidden
		*
			@apply font-extrabold
	.heading-1-up,.heading-2-up,.heading-3-up
		@apply uppercase
		*
			@apply uppercase
	.heading-2,.heading-2-up
		@apply text-2xl   font-bold
	.heading-3,	.heading-3-up
		@apply text-xl font-bold
	.heading-4
		@apply text-lg  font-bold

	.heading-5
		@apply text-base  font-bold leading-140
	.heading-5-underline
		@apply text-base  underline font-bold leading-140
	.body-1
		@apply text-base leading-140
	.body-2
		@apply text-xl leading-140 
	.body-3
		@apply text-sm leading-140
	.body-4
		@apply text-lg leading-140


	.title-gradient 
		-webkit-background-clip: text
		-webkit-text-fill-color: transparent
		margin-bottom: r(-25px)
		background-image: linear-gradient(180deg, #0066FF 0%, rgba(0, 61, 153, 0) 100%)
		@apply  w-fit uppercase whitespace-nowrap leading-none font-black  opacity-10 text-6xl
		@screen sm
			@apply text-8xl  
			margin-bottom: r(-30px)
		@screen max-sm
			margin-bottom: r(-20px)
			@apply text-5xl whitespace-nowrap !#{important}
		@media screen and ( min-width: 1300px)
			@apply pl-3 text-9xl 
			margin-bottom: r(-55px)
		
		&.white
			@apply  opacity-20
			background-image: linear-gradient(180deg, #FFFFFF 0%, rgba(255, 255, 255, 0) 100%)

	.heading-1, .heading-2, .heading-3, .heading-4, .heading-5,.heading-6,.heading-7,
	.body-1, .body-2, .body-3, .body-4, .body-5
		@include text-base
