.row 
	@apply flex flex-wrap
	justify-content: flex-start
.col
	flex: 0 0 auto
	@apply flex flex-col
	padding: 0 20px
	.col
		margin-bottom: 0
.row
	@media screen and ( max-width: 1024px)
		margin: 0 -10px -20px -10px
		>.col
			padding: 0 10px
			margin-bottom: 20px
	@screen lg
		margin: 0 -20px -40px -20px
		>.col
			margin-bottom: 40px
.no-gutter
	@apply m-0 p-0 !#{important}
	> *
		@apply m-0 p-0 !#{important}
.col-match-height
	@apply flex w-full flex-col h-full
.container, .container-fluid
	width: 100%
	padding-right: 20px
	padding-left: 20px
	margin-right: auto
	margin-left: auto