.searchresults
	@apply relative z-20
	main
		@apply bg-white
.suggestsearch
	@apply absolute-x top-full  z-50 bg-white p-8 text-center overflow-hidden w-full shadow-lg pt-4 rounded-4
.input-group-text
	@apply hidden
.search-page
	@screen xl
		padding: r(180px) 0 r(60px) !important
	h1
		letter-spacing: -2px
		margin-bottom: 20px
		font-size: 45px
		line-height: 55px
		font-weight: 500
		color: #333333
		@media (max-width: 992px)
			font-size: 24px
			line-height: 30px
		&.center
			text-align: center
	.btn-reindex
		display: none
	.searchcontrols
		.form-inline
			position: relative
			&:before
				position: absolute
				content: "\f002"
				font-family: 'Font Awesome 6 Pro'
				top: 0
				right: 10px
				z-index: 1
				font-size: 24px
				color: #4285f4
				width: 44px
				height: 44px
				display: flex
				align-items: center
				justify-content: center
				pointer-events: none
		.frm-btn
			position: absolute
			top: 0
			right: 10px
			background-color: rgba(0,0,0,0)
			z-index: 2
	input[type="text"]
		width: 100%
		height: 100%
		padding-left: 20px
		padding-right: 54px
		border: 1px solid #dfe1e5
		background-color: #fff
		color: #333
		font-size: .875rem
		font-weight: 400
		height: 44px
		border-radius: 22px
		box-shadow: 0 0 0 0px rgba(0,0,0,0)
		transition: .25s cubic-bezier(.4,0,.2,1)
		outline: none
		&:focus
			box-shadow: 0 1px 6px 0 rgba(32,33,36,0.28)
			border-color: rgba(223,225,229,0)
	input[type="submit"]
		border-radius: 2px
		user-select: none
		background-color: rgba(0,0,0,0)
		border: 0
		box-shadow: none
		cursor: pointer
		text-align: center
		display: flex
		align-items: center
		justify-content: center
		height: 40px
		outline: none
		width: 44px
		height: 44px
		border-radius: 50%
		font-size: 0
	.text-danger
		margin-top: 20px
		font-size: 15px
		color: #333
		font-style: italic
		font-weight: 600
	.searchresultsummary
		margin-bottom: 20px
	.searchresults
		margin-top: 30px
		.modulepager
			&:first-child
				display: none
	.searchresultlist
		margin-bottom: 30px
	.searchresult
		margin-bottom: 30px
		box-shadow: 0 1px 6px rgba(32, 33, 36, 0.28)
		padding: 16px
		border-radius: 8px
		&:last-child
			margin-bottom: 0
		h3
			font-size: 18px
			line-height: 1.33
			font-weight: 400
			margin-bottom: 10px
			a
				color: #1A0DAB
				text-decoration: none
				font-weight: 500
				// &:hover
				// 	text-decoration: underline
		.searchresultdesc
			color: #545454
			line-height: 1.54
			word-wrap: break-word
			font-size: small
			font-weight: 400
			margin-bottom: 10px
		.searchterm
			color: #6a6a6a
			font-weight: bold
			font-style: normal
			line-height: 1.54
			word-wrap: break-word
			font-size: small
		hr
			display: none !important
	.search-title
		@apply text-24  font-bold mb-4 block uppercase
	.text-main
		@apply font-normal text-18 text-26 normal-case
	.searchresultsummary
		@apply text-16  font-semibold
	.kw-desc
		@apply font-bold 
.keyword-list
	ul
		@apply grid grid-cols-1 gap-4
		@screen md
			@apply  grid-cols-2
		li
			@apply border border-grey-50 overflow-hidden rounded-2 transition
			&:hover
				@apply border-primary-1
	.title
		@apply line-clamp-2
.keyword-list
	max-height: 300px
	padding-right: 15px
	overflow-y: auto
.keyword-list::-webkit-scrollbar
	background: #D5E0EC
	width: 4px !important
	border-radius: 4px
.keyword-list::-webkit-scrollbar-track
	-webkit-box-shadow: inset 0 0 4px #D5E0EC
	background-color: #D5E0EC
	border-radius: 4px
.keyword-list::-webkit-scrollbar-thumb
	background-color: #005BAA
	border-radius: 30px !important
	@media screen and ( max-width:1024px)
		height: auto
@-moz-document url-prefix()
	.keyword-list
		scrollbar-width: thin
		scrollbar-color: #005BAA #D5E0EC
.search-toggle
	@apply w-10 h-10 transition cursor-pointer
	&::before
		content: "\f002"
		@apply  flex-center font-Awesome6 pointer-events-none  z-10  transition  w-full h-full font-light   text-primary-1   text-2xl
.search-overlay
	@apply w-full flex-center mx-auto
	max-width: r(720px)
	.container
		@apply  flex-center  p-0 
		position: static !important
.searchbox
	@apply   x-start  mx-auto w-full  pointer-events-none    fixed  z-999  opacity-0 transition 
	left: 50%
	transform: translateX(-50%)
	@screen max-xl
		@apply top-[60px] border-t border-t-grey-100
	@screen xl
		top: 124px
	&.is-open
		@apply opacity-100 pointer-events-auto
		z-index: 9996
	.search-overlay
		@apply   p-[32px] relative
		&::after
			content: ''
			@apply block absolute pointer-events-none left-0 bottom-0 z-10  transition w-full h-full bg-white shadow-lg overflow-hidden rounded-4
			
	input[type='text']
		@apply w-full h-full bg-transparent px-5  border border-grey-200 rounded-full overflow-hidden relative z-50
		height: 45px
		@media screen and ( max-width: 1200px)
			@apply bg-white     font-semibold
		@screen xl
			@apply w-full
		&:placeholder
			@apply  opacity-70 
	button
		@apply flex-center  transition  w-[90px] h-[45px]  absolute-y right-[33px] rounded-full overflow-hidden relative z-50
		&:hover
			@apply bg-primary-600
			em
				@apply  text-white
		em
			@apply text-primary-950  leading-none transition
.error-message
	@apply font-bold text-center  leading-tight

.search-wrap
	@apply flex-center  relative  transition cursor-pointer
	z-index: 120
	em
		@apply text-white text-24
