.language-wrap
	@apply rounded-full relative  p-0  
	z-index: 900
	.wpml-ls-display
		@apply hidden
	ul
		@apply flex-center 
	
	.wpml-ls-link
		@apply flex-center 
		width: auto
		&:hover
			span
				@apply underline
		span
			@apply uppercase text-base  border-b border-transparent
	.wpml-ls-current-language
		span
			@apply underline font-bold
	img
		@apply hidden !#{important}
	.wpml-ls-item 
		@apply px-2 flex-center relative
		&:last-child
			@apply pr-0
			&::after
				@apply hidden
		&::after
			content: ''
			@apply block absolute-y right-0 pointer-events-none  z-10  transition w-[1px] h-[22px] bg-primary-1 
			@screen xl
				@apply h-5
		span
			@apply text-primary-1 
.white
	.wpml-ls-item 
		&::after
			@apply bg-white
		span
			@apply text-white