.nav-primary-menu
	@apply  relative z-30 w-full h-full flex-end 
	.nav
		@apply gap-4  h-full flex flex-col  w-full 
		>.menu-item
			@apply z-30 relative  flex-center
			>.title
				@apply w-full 
				&::after
					content: ''
					@apply block absolute-center pointer-events-none z-10  transition w-0 h-full bg-white
					transform:   translate(-50%,-50%) skew(-25deg) !important
				>a
					@apply py-2 relative z-50 w-full
			&:hover,&.current-menu-item,&.current-category-ancestor,&.current-menu-ancestor,&.current-menu-parent,&.current-category-parent,&.current-money-map-category-ancestor,&.current-post-ancestor,&.current-project-ancestor
				>.title
					&::after
						@apply w-full
					>a
						@apply text-primary-1
					.icon
						&::after
							@apply text-primary-1
				&:first-child
					a
						
						&::after
							@apply text-primary-1
	.icon
		@apply   z-40  transition  w-[12px]  h-[32px] pointer-events-auto  mr-4
		&::after
			content: '\f078'
			@apply flex-center  pointer-events-none  z-10  transition w-full h-full  text-lg  font-bold  font-Awesome6 text-white
	.menu-item-has-children
		@apply flex flex-col
		>.sub-menu
			@apply  block z-30  w-full transition overflow-hidden     p-6 bg-primary-1  pl-8
			li
				&:not(:last-child)
					@apply m-0 mb-3
					@screen 2xl
						@apply  mb-4
				&.current-menu-item,&.current-category-ancestor,&.current-menu-ancestor,&.current-menu-parent,&.current-category-parent,&.current-money-map-category-ancestor,&.current-post-ancestor,&.current-project-ancestor,&:hover
					a
						@apply underline  text-white
				a
					@apply flex-start text-lg    transition   py-0 text-white font-normal whitespace-nowrap
				.title
					@apply flex-start  p-0
					&::before
						@apply hidden
	.menu-item
		@apply relative z-10  block  px-8
		&:first-child
			@apply ml-0
		&:last-child
			@apply mr-0
		.title
			@apply flex-center relative h-full px-10
			a
				@apply relative
		&.current-menu-item,&.current-category-ancestor,&.current-menu-ancestor,&.current-menu-parent,&.current-category-parent,&.current-money-map-category-ancestor,&.current-post-ancestor,&.current-project-ancestor
			>.title
				>a
					@apply text-primary-1 
					&::after
						@apply bg-white
		a
			@apply  relative  flex-start    cursor-pointer transition  leading-140 font-normal text-white uppercase 
			@media screen and ( min-width: 1200px)
				@apply text-xl
		